.colorWrap {
  background-color: rgb(226, 222, 222);
  position: sticky;
  top: 0;
  z-index: 3;
}

.navbar {
  height: 5rem;
  width: 100vw;
  max-width: 75rem;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 3;
  background: white;
  background-color: rgb(226, 222, 222);
}

.logo {
  object-fit: cover;
  height: 4rem;
  width: 4rem;
}

.desktopMenuListItem {
  margin: 1rem;
  cursor: pointer;
}

.desktopMenuListItem:hover {
  color: #304c89;
  padding-bottom: 0.5 rem;
  border-bottom: 3px solid #304c89;
}

.desktopMenuBtn {
  background: black;
  color: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  border-radius: 2rem;
}

.desktopMenuImg {
  object-fit: cover;
  height: 1rem;
  width: 1rem;
  margin: 1rem;
  color: white;
}

.active {
  color: #304c89;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid #304c89;
}
.mobMenu {
  display: none;
  object-fit: cover;
  height: 4rem;
}

.navMenu {
  position: absolute;
  top: 4rem;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  height: fit-content;
  min-width: 15rem;
  background: rgb(236, 232, 232);
  border-radius: 1rem;
}

.listItem {
  color: black;
  padding: 0.5rem 3rem;
  margin: 0.25rem;
  background: rgb(226, 222, 222);
}

@media screen and (max-width: 720px) {
  .mobMenu {
    display: flex;
  }

  .desktopMenu {
    display: none;
  }

  .desktopMenuBtn {
    display: none;
  }
}
